body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: #202020;
}
body{
  background-color: #222222;
}
/* Split the screen in half */
.split {
  height: 88%;
  width: 50%; /* change this value to 100% to make the output take up entire screen */
  position: fixed;
  z-index: 1;
  /*top: 10%; /*height of toolbar*/
  overflow-x: hidden;
}

/* Control the left side */
.left {
  left: 0;
  display: flex;
  flex-direction: column;
}

/* Control the right side */
.right {
  right: 0;
  background-color: #222222;
  
}

textarea {
  width: 98%;
  display: flex;
  flex: 1 1;
  border: none;
  resize: none;
  background-color: #222222;
  color: white;
  font-size: 16px;
  padding-left:5px;
  padding-right: 5px;
  margin-left: 5px;
  
}

::-webkit-scrollbar { 
  display: none; 
}

/*TOOLBAR*/
#TOOLBAR{
  display: flex;
  background-color: rgb(17, 17, 17);
  color: white;
  margin-top: 0;
  margin-bottom: 0;
  height: 70px;
}
#TOOLBAR ul{
  width: 100%;
}
#TOOLBAR ul li{
  width: 80%;
  float: right;
  margin-left: 0;
  padding: 0;
  list-style: none;
}



/* BUTTON COMPILE */
.buttoncompile {
  font-family: "Lucida Console", "Lucida Sans Typewriter", monaco, "Bitstream Vera Sans Mono", monospace;

  border-radius: 4px;
  background-color: white;
  border: none;
  color: rgb(255, 69, 0);
  font-weight: bold;
  text-align: center;
  font-size: 18px;
  padding: 5px;
  width: 18%;
  transition: all 0.5s;
  cursor: pointer;
  margin: 5px;
  left:0;
}
.buttoncompile span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}
.buttoncompile span:after {
  content: '\BB';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}
.buttoncompile:hover span {
  padding-right: 25px;
}
.buttoncompile:hover span:after {
  opacity: 1;
  right: 0;
}



